.container {
	position: relative;
	display: flex;
	align-items: center;
	flex-wrap: wrap;
}
.week_day {
	flex-basis: calc(100% / 7);
	padding: 0.75rem;
	text-align: center;
	text-transform: uppercase;
	font-size: 0.75em;
	font-weight: 400;
}
@media (min-width: 40.01rem) {
	.week_day {
		padding: 0.75rem 0.5rem;
	}
}



